
















































import Vue from "vue";
import * as VueGoogleMaps from "gmap-vue" 
import { gmapApi,loadGmapApi } from "gmap-vue";
Vue.use(VueGoogleMaps);
export default Vue.extend({
  
  computed: {
    google: gmapApi,
  },
  beforeMount() {    
    if(this.google) { return};
    let gmapApiMapKey = this.mapKey && this.mapKey.trim() != "" ?  this.mapKey.trim() : "AIzaSyDA4KFrYr_Ux1HKyKqIAQB0fMU7qaT_5DA";
    loadGmapApi({
        key: gmapApiMapKey,
        libraries: "places"
    });
  },
  data: () => ({
    statusText: "",
    infoWindowPos: null,
    infoWinOpen: false,
    currentMidx: null,
    markerIcon: {
      url: ""
    },
    infoOptions: {
      content: "",
      pixelOffset: {
        width: 0,
        height: -35,
      },
    },
    center: { lat: 49, lng: 21 },
  }),
  name: "QMap",
  props: {
    center: {
      type: Object,
    },
    markers: {
      type: Array,
      default: [{}],
    },
     zoom: {
      type: Number,
      default:6
      
    },
    width:{
      type:String
    },
    height:{
      type:String
    },
    mapType:{
      type: String,
    },
    mapKey:{
      type: String,
    },
    markerIcon: {
      type: String
    }
  },
  mounted() {},
  destroyed() {
      this.$el?.remove();
   },
  methods: {
    toggleInfoWindow: function (marker, idx) {
      
      const clickedPosition = {
        position: marker.position,
      };
      this.$emit("click:marker", clickedPosition);
      this.infoWindowPos = marker.position;
      this.infoOptions.content = marker.infoText;
 
     
        if(marker.infoText!= undefined){
              this.infoWinOpen = true;
        this.currentMidx = idx;
        }
        else{
          this.infoWinOpen=false
        }
        
      
    },
    mark(event) {
      
      
      this.statusText = " ";
      const clickedPosition = {
        position: { lat: event.latLng.lat(), lng: event.latLng.lng() },
      };
      this.$emit("click:map", clickedPosition)
      this.markers.push(clickedPosition);
      this.statusText = "";
    },
  },
});
